'use client'

import { useAuth } from '@/contexts/AuthContext'
import { useRouter } from 'next/navigation'
import { useEffect, useState } from 'react'
import LoadingSpinner from './LoadingSpinner'

interface AdminGuardProps {
  children: React.ReactNode
}

export default function AdminGuard({ children }: AdminGuardProps) {
  const { user, isAdmin, isLoading } = useAuth()
  const router = useRouter()
  const [isChecking, setIsChecking] = useState(true)

  useEffect(() => {
    console.log('üîê AdminGuard Debug:', { 
      user, 
      isAdmin, 
      isLoading,
      token: localStorage.getItem('access_token')
    })
    
    if (!isLoading) {
      // Si pas d'utilisateur
      if (!user) {
        console.log('‚ùå Pas d\'utilisateur, redirection login')
        router.push('/login?redirect=/admin')
      } 
      // Si utilisateur mais pas admin
      else if (!isAdmin) {
        console.log('‚ùå Utilisateur non admin, redirection home')
        router.push('/')
      } 
      // Si tout est bon
      else {
        console.log('‚úÖ Acc√®s admin autoris√©')
        setIsChecking(false)
      }
    }
  }, [user, isAdmin, isLoading, router])

  // Pendant le chargement
  if (isLoading || isChecking) {
    console.log('‚è≥ AdminGuard - Loading...')
    return <LoadingSpinner />
  }

  // Si on arrive ici, c'est bon
  console.log('üéØ AdminGuard - Rendu des enfants')
  return <>{children}</>
}